import './style/lophoc.css'
import React from "react";
import { Link } from "react-router-dom";

export const ClassRoom = () => {
    return (
        <div class="main-content">
            <div class="course-content">
                <div class="course-content-body">
                    <div class="course-name-header vertical-box">
                        <h2 class="course-name">Khóa học: Lập trình Java căn bản</h2>
                        <div class="university-name">
                            <span class="university-text">
                                <span>Đại Học Công Nghệ Thông Tin Truyền Thông Việt - Hàn</span>
                            </span>
                        </div>
                    </div>
                    <div class="course2 non-learning">
                        <div class="course-border">
                            <div class="label-text">Bài 1</div>
                        </div>
                        <div class="course-content-2">
                            <div class="material">
                                <div class="flex-1 description">
                                    <div class="item-name">Giới thiệu</div>
                                    <div class="item-detail description">
                                        <span>Giới thiệu giảng viên, ngôn ngữ Java,...</span>
                                    </div>
                                </div>
                                <div class="align-self-center">
                                    <Link to='/inclass' class="button-primary roomy">
                                        Vào học
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="course2 in-progress">
                        <div class="course-border">
                            <div class="label-text">Bài 2</div>
                        </div>
                        <div class="course-content-2">
                            <div class="material">
                                <div class="flex-1 description">
                                    <div class="item-name">Các biến, phương thức</div>
                                    <div class="item-detail description">
                                        <span>Định nghĩa phương thức, khai báo biến,...</span>
                                    </div>
                                </div>
                                <div class="align-self-center">
                                    <Link to='/inclass' class="button-primary roomy">
                                        Vào học
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="course2 finish">
                        <div class="course-border">
                            <div class="label-text">Bài 3</div>
                        </div>
                        <div class="course-content-2">
                            <div class="material">
                                <div class="flex-1 description">
                                    <div class="item-name">Lập trình hướng đối tượng</div>
                                    <div class="item-detail description">
                                        <span>Khái niệm: Hướng đối tượng là gì?,...</span>
                                    </div>
                                </div>
                                <div class="align-self-center">
                                    <Link to='/inclass' class="button-primary roomy">
                                        Vào học
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ClassRoom;
